/** 基礎功能色設定 */
$color-white: #fff;
$color-dark: rgba(0, 0, 0, 0.8);
// 淺灰
$color-light-gray: #BDBDBD;

// 主要色
$primary-color: #FF7C02;
// 主要色變形
$primary-variant-color: #FFF8F2;
// 次要色
$secondary-color: #C16E00;
// 次要色變形
$secondary-variant-color: #e6f3f7;
// 第三色
$third-color: #f3202e;
// 第三色變形
$third-variant-color: #eed7ed;
// danger
$danger-color: #E73D4A;

/** 基礎元件設定 */
$font-sz: (
  xl: 30px,
  lg: 18px,
  md: 16px,
  sm: 14px,
  xs: 12px,
);

$font-weight: (
  fatty: 600,
  normal: normal,
);

/** 常用元件設定 */
/* base btn start */
$btn: (
  border-radius: 7px,
  shadow: 1px 1px 2px 0 rgba(21, 28, 42, 0.17),
);
$btn-height: (
  lg: 40px,
  md: 35px,
  sm: 30px,
);
$btn-width: (
  xl: 200px,
  lg: 150px,
  md: 120px,
  sm: 90px,
  xs: 60px,
);
/* base btn end */

/** base card start*/
$base-card: (
  background-color: $color-white,
  box-shadow: none,
  padding: 0,
  border: 0,
  border-radius: 6px,
);

$base-card-header: (
  align-self: center,
  font-size: map-get($font-sz, lg),
  font-weight: map-get($font-weight, fatty),
  padding: 0 15px,
);

$base-card-content: (
  padding: 10px 15px,
);
/** base card end*/

/* base table start */
$base-table: (
  // hover 顏色
  hover-background-color: darken($color-light-gray, 15%),
  // 間隔色
  even-col-color: $color-light-gray,
  // 圓角
  radius: 7px,
  // 陰影
  shadow: 1px 2px 4px #d5d5d5,
  // 表頭、內容（th、td） 間隔
  padding: 4px 8px,
  // 手機版表頭、內容（th、td） 間隔（flex table 專用）
  mo-padding: 10px 8px,
  // 幾px以下使用手機版（flex table 專用）
  break-point: 991px,
  // 欄位下方的border-bottom（flex table 專用）
  mo-border-bottom-width: 1px
);

/* base tab start */
$base-tab: (
  // 顏色(merc-tab 未傳入 mercType 時有效)
  tab-color: $primary-color,
  // tab 編框
  tab-border-width: 1px,
  // content 編框
  content-border-width: 10px,
  // 圓角
  radius: 7px,
  // 字體大小
  font-size: map-get($font-sz, lg)
);
/* base tab end */

/* slide image start */
$slide-image: (
  image-background-color: $color-light-gray,
  focus-border: 2px solid $primary-color
);
/* slide image end */
